.component_navbar{
 width: 100%;
 height: 100%;
 overflow: hidden;
 position: absolute;
.container_navbar{
  position: absolute;
  top: 0;
  display: block;
  z-index: 1000;
  width: 100%;
  .container_icon-hamburguer{
    position: absolute;
    right: 2%;
    top: 20%;
  }
  .title_flag{
    border-radius: 5px;
    display: flex;
    align-items: center;
    height: 3em;
    margin: 5px;
    a{
      text-decoration: none;
      color: white;
    }
    h1{
      margin: 0;
      padding: 10px;
      border-radius: 5px;
    }
  }
  .navbar{
    padding: 10px 0px;
    border-radius: 5px;
    background: rgba(250px, 250px,250px, 0.5);
    backdrop-filter: blur(20px);
    height: 100vh;
    position: absolute;
    top: 0%;
    right: -60%;
    width: 40%;
    transition: right;
    transition-duration: 1s;
    z-index: 3;
    display: flex;
    flex-direction: column;
    
    .container_icon-exit{
      margin-left: 10px;
      text-align: center;
      position: absolute;
      right: 50px;
      width: 100%;
    }
    a{
      text-align: left;
      text-decoration: none;
      color: black;
      margin:2em 4em;
      width: 90%;
    }
    a:hover{
      border-bottom: 1px solid black;
    }
  }
  .is-active{
    right: 0;
    height: 100vh;
  }
  
  .no-active{
    display: none;
  }
}
}
.desaparece{
  display: none;
}
@media (min-width: 960px){
  .component_navbar{
    .container_navbar{
      height: 10%;
      display: flex;
      backdrop-filter: blur(20px);
      position: absolute;
      .title_flag{
        
      }
      .container_icon-hamburguer{
        display: none;
      }
      .navbar{
        backdrop-filter: none;
        justify-content: center;
        background: none;
        align-items: center;
        right: 0%;
        display: flex;
        width: 50%;
        height: 100%;
        a{
          height:80%;
          width: 10%;
          margin: 0;
          padding: 5px;
          line-height: 1;
        }
        .container_icon-exit{
            display: none;
        }
        a{
          color: white;
          padding: 5px;
        }
        a:hover{
         border-bottom: 1px solid white;
        }
      }
    }
  }
}